// initialize points
let gridSize = 80;
let gap = 0.50;
const coords = [];
const colors = [];
const points = [];
let x;
let y = 0;
let z;
let r;
let g;
let b;
let point = {
  position: {},
  rate: 0.0,
};
for (let i = -gridSize; i < gridSize; i += 1) {
  for (let j = -gridSize; j < gridSize; j += 1) {
      x = i * gap;
      z = j * gap;
      r = Math.random();
      g = Math.random();
      b = Math.random();
      point = {
        position: {
          x,
          y,
          z,
        },
        color: new THREE.Color(r, g, b),
      };
       points.push(point);
       coords.push(x, y, z);
       colors.push(r, g, b);
  }
}

// points
var pointSize = 0.1
let geo = new THREE.BufferGeometry();
geo.setAttribute("position", new THREE.Float32BufferAttribute(coords, 3));
geo.setAttribute("color", new THREE.Float32BufferAttribute(colors, 3));
let mat = new THREE.PointsMaterial({ size: pointSize, vertexColors: true });
let pointsObj = new THREE.Points(geo, mat);
let Noise = new ImprovedNoise();


  // Returns a Promise that resolves after "ms" Milliseconds
  var changeR = 15
  var changeG = 1
  var changeB = 35
  var changeNScale = 0.10
  const timer = ms => new Promise(res => setTimeout(res, ms))
  async function load (newValue, action, value) { // We need to wrap the loop into an async function for this to work)

      for (var i = 0; i <= newValue ; i++) {
        action
        // console.log(changeG)
        if (value == pointSize){
          return;
        }
        // console.log(changeR);
       await timer(50); // then the created Promise can be awaited
    }
  }
  
var colorScale = 0.2

function updatePoints(t) {

  const coords = [];
  const cols = [];
  let ns;
  let nScale = changeNScale;
  const yPosScale = 1.5;
  const lowColor = new THREE.Color(0.0, 0.0, 0.0);
  let highColor = new THREE.Color(changeR, changeG, changeB);
  // console.log(changeG)

  points.forEach((p, i) => {
    ns = Noise.noise(
      p.position.x * nScale,
      t, 
      p.position.z * nScale,
      );
    p.position.y = ns * yPosScale;
    p.color.lerpColors(lowColor, highColor, ns * colorScale);
    let { r, g, b } = p.color;
    cols.push(r, g, b);
    let {x, y, z } = p.position;
    coords.push(x, y, z);
  });
  geo.setAttribute("position", new THREE.Float32BufferAttribute(coords, 3));
  geo.setAttribute("color", new THREE.Float32BufferAttribute(cols, 3));
}
scene.add(pointsObj);

var contactIsUp = false

let clock = new THREE.Clock();
clock.start()
scene.fog = new THREE.Fog('black', 20, 30);
scene.fog = new THREE.Fog('black', 10, 2);
const timeMult = 0.0002;

function animate(timeStep) {
  requestAnimationFrame(animate);
  updatePoints(timeStep * timeMult);
  // console.log(`${changeR},${changeG},${changeB}`)

    // console.log(window.scrollY)

  let upIf =(colorToChange)=>{
    if(window.scrollY > 500 && !(colorToChange) && (contactIsUp === false)){
      return true
    }
  }

  if (upIf(changeR == 55)){ //Scroll down
    changeR+=1
  }

  if (upIf(changeG == 5)){ //Scroll down
    changeG+=0.5
  }

  if (upIf(changeB == 20)){ //Scroll down
    changeB-=0.5;
  }

  if (upIf(changeNScale >= 0.15)){ //Scroll down
    changeNScale+=0.001
  }

  if (window.scrollY > 500 && !(camera.position.y >= 4) && (contactIsUp === false)){ //Scroll down
    camera.position.y += 0.1
  }

  if (contactIsUp === true){
    document.getElementById('flexCont').style.opacity = "0"
  } else {
    document.getElementById('flexCont').style.opacity = "1"
  }

  let downIf =(colorToChange)=>{
    if(window.scrollY < 500 && !(colorToChange) && (contactIsUp === false)){
      return true
    }
  }
  if (downIf(changeR <= 15)){ // Scroll up
    changeR-=1
  }
  if (downIf(changeG <= 1)){ // Scroll up
    changeG-=0.5
  }
  if (downIf(changeB <= 35)){ // Scroll up
    changeB+=0.5
  }
  if (downIf(changeNScale<= 0.10)){ // Scroll up
    changeNScale-=0.001
  }
  if (window.scrollY > 500 && !(camera.position.y <= 2.265) && (contactIsUp === false)){ //Scroll down
    camera.position.y -= 0.1
  }
  
  renderer.render(scene, camera);
  // console.log(clock.getElapsedTime())
  if (clock.getElapsedTime() >= 0 && camera.position.y <= 2.265){
    camera.position.y += 0.01
    camera.position.z += 0.01
    camera.position.x += 0.01
    camera.lookAt(0,0,0)
  }
}

let scrollBefore = 0;
window.addEventListener('scroll',function(e){
    const scrolled = window.scrollY;
    if(scrollBefore > scrolled){
        // console.log(this.window.scrollY + ' up')
        scrollBefore = scrolled;
        if (scrolled < 500){
          document.querySelector("#ready").style.opacity = "1"
          document.querySelector("#aboutMe").style.opacity = "0"
          document.querySelector("#mySkills").style.opacity = "0"
        }
      }else{
        scrollBefore = scrolled;
        // console.log(this.window.scrollY + ' down')
        if (scrolled > 500){
          document.querySelector("#ready").style.opacity = "0"
          document.querySelector("#aboutMe").style.opacity = "1"
          document.querySelector("#mySkills").style.opacity = "1"
        }
    }
})
