// MODAL
const changePix =(R,G,B,delay)=>{
  changeR = R
  changeG = G
  changeB = B
  delay
}
let opacityChange =(element,value)=>{
  element.style.opacity = value
}
let transChange =(element,time)=>{
  element.style.transition = `all ${time}s ease-in-out`
}

const closeModal = () => {
  setTimeout(function() {
    changePix(5,5,5)
    colorScale = 0.05
    setTimeout(function() {
      changePix(5,4,10)
      colorScale = 0.05
      setTimeout(function() {
        changePix(10,2,20)
        colorScale = 0.10
        setTimeout(function() {
          changePix(15,1,35)
          colorScale = 0.15
        }, 10);
      }, 10);
    }, 10);
  }, 10);
  
  let contact = document.getElementById('contact')
  contactIsUp = false
  // document.getElementById('contactModal').remove()
  contact.innerText = 'Contact Me'
  document.getElementById('gradientBorder').style.left = "1vw"
  opacityChange(document.getElementById('social'),1)
  document.getElementById('social').style.filter = 'grayscale(0%)'
  
  let name = document.getElementById('name')
  let title = document.getElementById('title')
  let projects = document.getElementById('projects')
  opacityChange(name,1)
  opacityChange(title,1)
  opacityChange(projects,1)

  setTimeout(function() {
    transChange(name,1)
    transChange(title,1)
    transChange(projects,1)
  }, 500);

  projectMouseOver()
  document.getElementById('contactModal').remove()
  contact.addEventListener('click', contactPopUp)
}


const contactPopUp = () => {
  let copy =()=>{
    navigator.clipboard.writeText('NoahKeithDev@gmail.com'); 
  }
  let copyToClip =()=> {
    copy()
    document.getElementById('clickToCopy').innerText = `Copied!`
  }

  
  myInput = document.getElementById("myInput")
  document.getElementById('gradientBorder').style.left = "2vw"
  document.getElementById('ready').innerHTML += `
  <div id="contactModal">
    <div id="contactMain">
      <p id='clickToCopy'>click to copy</p>
      <p id="copyContact">NoahKeithDev@gmail.com</p>
    </div>
  </div>`
  let copyContact = document.getElementById('copyContact')
  let contactMain = document.getElementById('contactMain')
  copyContact.addEventListener("click", copyToClip)
  contactMain.addEventListener("click", copyToClip)

  let copyHover =()=>{
    // console.log(copyContact)
    copyContact.style.fontSize = `110%`
  } 
  let copyHoverOut =()=>{
    // console.log(copyContact)
    copyContact.style.fontSize = `100%`
  } 
  if (!isSafari){
    copyContact.addEventListener("mouseover", copyHover)
    copyContact.addEventListener("mouseout", copyHoverOut)
  }

  let name = document.getElementById('name')
  let title = document.getElementById('title')
  let projects = document.getElementById('projects')
  let contact = document.getElementById('contact')
  let social = document.getElementById('social')

  opacityChange(social,0.3)
  social.style.filter = 'grayscale(90%)'

  contact.innerText = 'Close'
  contact.addEventListener('click', closeModal)
  contactIsUp = true

  setTimeout(function() {
    changePix(13,1,30)
    colorScale = 0.15
    setTimeout(function() {
      changePix(8,3,20)
      colorScale = 0.10
      setTimeout(function() {
        changePix(5,4,10)
        colorScale = 0.08
        setTimeout(function() {
          changePix(5,5,5)
          colorScale = 0.05
        }, 10);
      }, 10);
    }, 10);
  }, 10);

  transChange(name,0.1)
  transChange(title,0.1)
  transChange(projects,0.1)

  opacityChange(name,0.1)
  opacityChange(title,0.1)
  opacityChange(projects,0.1)
}
